
.icon-success {
  width: 64px;
  height: 64px;
  margin: 0 auto;
  background-image: url(icon-success.png);
  background-size: 64px 64px;
}
h3.txt-success {
  font-size: 18px;
  line-height: 24px;
  color: #4caf50;
  text-align: center;
}
.article-file {
  width: 128px;
  height: 128px;
  background-image: url(icon-file.png);
  background-repeat: no-repeat;
  background-size: 128px 128px;
}
.article-file > input {
  display: block;
  width: 128px;
  height: 128px;
  opacity: 0;
}
.mu-text-field.focus-state {
  color: #4caf50;
}



