.loading-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
}
.loading-wrap .loading {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  left: 50%;
  margin-left: -30px;
  top: 50%;
  margin-top: -30px;
  background-color: #4CAF50;
}
.operationSuccess,
.operationError {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
}
.operationSuccess .success-wrap,
.operationError .success-wrap,
.operationSuccess .error-wrap,
.operationError .error-wrap {
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 10px;
  left: 50%;
  margin-left: -50px;
  top: 50%;
  margin-top: -50px;
  padding: 10px;
}
.operationSuccess .success-wrap > i,
.operationError .success-wrap > i,
.operationSuccess .error-wrap > i,
.operationError .error-wrap > i {
  display: block;
  width: 100%;
  height: 64px;
  background-size: 64px 64px;
  background-repeat: no-repeat;
  background-position: center center;
}
.operationSuccess .success-wrap > span,
.operationError .success-wrap > span,
.operationSuccess .error-wrap > span,
.operationError .error-wrap > span {
  display: block;
  width: 100%;
  height: 16px;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  text-align: center;
}
.operationSuccess .success-wrap,
.operationError .success-wrap {
  background-color: #4CAF50;
}
.operationSuccess .error-wrap,
.operationError .error-wrap {
  background-color: #f44336;
}
