
.flex-gopage-l {
  padding: 15px 0 15px 10px;
  overflow: hidden;
}
.flex-gopage-r {
  padding: 15px  10px 0 0;
  overflow: hidden;
}
.flex-gopage-r > h2 {
  margin: 0;
  font-size: 16px;
  font-weight: normal;
  color: #4c4c4c;
}
.border-pd {
  padding-bottom: 10px;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.border-pd {
    border-bottom: 0.5px solid #b2b2b2;
}
}
@media screen and (-webkit-min-device-pixel-ratio: 3) {
.border-pd {
    border-bottom: 0.333333px solid #b2b2b2;
}
}
.gopage-money {
  font-size: 16px;
  color: #f64646;
  line-height: 25px;
}
.icon-gopage-money {
  display: inline-block;
  vertical-align: top;
  height: 25px;
  width: 25px;
  margin-right: 5px;
  background-image: url(icon-gopage-money.png);
}
.icon-grey-eye {
  display: inline-block;
  vertical-align: top;
  height: 25px;
  width: 25px;
  margin-right: 5px;
  background-image: url(icon-grey-eye.png);
}



