
.icon-watch {
  height: 40px;
  background-image: url(icon-watch.png);
}
.icon-leftMessage {
  height: 40px;
  background-image: url(icon-leftMessage.png);
}
.mu-tab-text {
  color: #4c4c4c;
}
.mu-tab-active .mu-tab-text {
  color: #4caf50;
}
.fixed-tabs {
  position: fixed;
  top: 45px;
}
#writeTripWrap {
  width: 100%;
  position: absolute;
  top: 93px;
  bottom: 45px;
  padding-bottom: 20px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
#showPhotoWrap {
  position: absolute;
  top: 93px;
  bottom: 45px;
  width: 100%;
  overflow: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.write-Article {
  position: fixed;
  right: 20px;
  bottom: 65px;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background-image: url(icon-write.png);
  background-size: 64px 64px;
  box-shadow: 0 14px 45px rgba(0, 0, 0, 0.247059), 0 10px 18px rgba(0, 0, 0, 0.219608);
}
.icon-sharImage {
  background-image: url(icon-sharImage.png);
}



