
.icon-del {
  background-image: url(icon-del.png);
}
.mu-checkbox {
  line-height: 1em;
}
.mu-checkbox-icon-checked {
  color: #4CAF50;
}
.mu-checkbox input[type="checkbox"]:checked + .mu-checkbox-wrapper .mu-checkbox-icon-uncheck {
  color: #4CAF50;
}
.shop-list-content {
  position: absolute;
  bottom: -15px;
  right: 0;
  width: 110px;
  height: 50px;
  line-height: 50px;
}
.shop-list-content > i {
  display: inline-block;
  vertical-align: top;
  height: 50px;
}



